@import url('https://fonts.googleapis.com/css2?family=DynaPuff:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  font-family: 'DynaPuff', cursive;
  box-sizing: border-box;
}


.main_container {
  width: 100%;
  height: 100vh;
  background-color: #edede9;
}

.main_box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.top_box {
  width: 405px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  grid-row-gap: 5px;
}

.top_box p {
  font-size: 40px;
  font-weight: 500;
  word-spacing: 5px;
  color: #868383;
  margin: 0;
}

.line {
  width: 85%;
  height: 2px;
  background-color: #868383;
}

.game_box {
  display: grid;
grid-template-columns: repeat(3, 1fr);
grid-template-rows: repeat(3, 1fr);
margin-top: 20px;
z-index: 5;
}

.btn {
  width: 150px;
  height: 140px;
  border: 15px solid #1a1b16;
  border-radius: 25px;
  margin-left: -15px;
  margin-top: -12px;
  cursor: pointer;
  transition: 0.2s all ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover {
  background-color: #E3E3DD;
  box-shadow: 0 0 8px 5px rgba(0, 0, 0, 30%);
}

.cross {
  color: #3399ff;
  font-size: 100px;
}

.circle {
  width: 50px;
  height: 50px;
  border: 16px solid #ff3333;
  border-radius: 50%;
}



.player_container{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -100px;
  z-index: 0;
}

.player_box {
  margin: 0 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 15px;
}

.player_img_box img {
  width: 140px;
  border-radius: 50%;
  box-shadow: 0 0 8px 6px rgba(0, 0, 0, 20%);
}

.player_details {
  padding: 10px 40px;
  border: 2px solid #AEADAD;
  border-radius: 15px;
  background-color: #E3E3DD;
}

.player_details p {
  font-size: 18px;
  font-weight: 600;
  color: #3399ff;
  margin: 0;
}

.current_player {
  background-color: #D1D1C7;
  box-shadow: 0 0 8px 8px rgba(0, 0, 0, 15%);
}






.modal_backgound {
  width: 100%;
  height: 100vh;
  background: rgba(30, 39, 46, 0.96);
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 900;
  display: flex;
  justify-content: center;
  padding-top: 150px;
  padding-bottom: 40px;
  overflow: auto;
}

.modal_container {
  background-color: #fff;
  padding: 40px;
  height: max-content;
  border: 1px solid #D1D1C7;
  /* width: 727px; */
  min-width: 250px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  grid-gap: 25px;
}

.modal_top_section p {
  color: #868383;
  font-size: 40px;
  text-align: center;
  margin: 0;
}

.modal_bottom_section {
  display: flex;
  align-items: center;
  justify-content: center;
}

.restart_btn {
    min-width: 90px;
    background-color: #7840f2;
    color: #fff;
    padding: 12px 30px;
    border-radius: 0.313rem;
    font-size: 25px;
    font-weight: 400;
    border: none;
    cursor: pointer;
    letter-spacing: 1px;
    font-family: 'DynaPuff', cursive;
}

.restart_btn:hover {
  background-color: #9a70f4;
}

@media screen and (max-width: 1100px) {
  .player_container {
    margin-top: 0;
  }

  .player_box {
    margin: 0 40px;
  }
}

@media screen and (max-width: 900px) {
  .top_box {
    width: 270px;
  }

  .top_box p {
    font-size: 30px;
  }

  .line {
    width: 97%;
  }

  .btn {
    width: 100px;
    height: 90px;
    border: 10px solid #1a1b16;
    border-radius: 15px;
    margin-left: -10px;
    margin-top: -8px;
  }

  .cross {
    font-size: 60px;
  }

  .circle {
    width: 30px;
    height: 30px;
    border: 12px solid #ff3333;
  }

  .player_img_box img {
    width: 70px;
  }

  .player_details {
    padding: 8px 20px;
  }

  .player_details p {
    font-size: 16px;
  }
}

@media screen and (max-width: 600px) {
  .player_container {
    margin-top: 30px;
  }

  .player_box {
    margin: 0 15px;
  }

  .modal_container {
    padding: 50px 16px;
  }

  .modal_top_section p {
    font-size: 30px;
  }

  .restart_btn {
    font-size: 18px;
  }
}

@media screen and (max-width: 450px) {
  .player_box {
    grid-gap: 8px;
  }

  .player_img_box img {
    width: 60px;
  }

  .player_details {
    padding: 6px 15px;
  }
}